	-ms-touch-action: none;
}

body,
canvas,
div {
	display: block;
	outline: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	-khtml-user-select: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

body {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 0;
	border: 0;
	margin: 0;

	cursor: default;
	color: #000000;
	background-color: #333;

	text-align: center;
	font-family: Helvetica, Verdana, Arial, sans-serif;

	display: flex;
	flex-direction: column;
	overflow: hidden;
}

canvas {
	background-color: rgba(0, 0, 0, 0);
}

a:link,
a:visited {
	color: #666;
}

a:active,
a:hover {
	color: #666;
}

p.header {
	font-size: small;
}

p.footer {
	font-size: x-small;
}

#splash {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #171717 url(../assets/image/pt/startup.jpg) no-repeat center;
	background-size: 45%;
	background-position: 50%;
	background-size: cover;
}


#splash {
	position: absolute;
	left: 0;
	top: 0;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
}

#ui-root {
	position: absolute;
	left: 50%;
	transform-origin: center center;
}


#footer-wrapper {
	position: absolute;
	bottom: 0;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#footer-text-img {
	width: 40%;
	margin-bottom: 0px;
}

#footer-copyright-image-id {
	width: 50%;
	margin-bottom: 0;
}


.progress-bar {
	position: absolute;
	left: 27.5%;
	top: 80%;
	height: 3px;
	padding: 2px;
	width: 45%;
	border-radius: 7px;
	box-shadow: 0 1px 5px #000 inset, 0 1px 0 #444;
}

.progress-bar span {
	display: block;
	height: 100%;
	border-radius: 3px;
	transition: width .4s ease-in-out;
	background-color: #3dc5de;
}

.stripes span {
	background-size: 30px 30px;
	background-image: linear-gradient(135deg, rgba(255, 255, 255, .15) 25%, transparent 25%,
			transparent 50%, rgba(255, 255, 255, .15) 50%, rgba(255, 255, 255, .15) 75%,
			transparent 75%, transparent);

	animation: animate-stripes 1s linear infinite;
}

@keyframes animate-stripes {
	0% {
		background-position: 0 0;
	}

	100% {
		background-position: 60px 0;
	}
}

#background-img {
	/* background-image: url("../assets/image/pt/back.jpg"); */
	background-position: 50%;
}
</style>
