	position: fixed;
}

#progress-bar-content {
	background: #59e390;
	width: 0%;
	height: .6vh;
	min-height: 5px;
}

.progress-bar-image {
	position: fixed;
	transform: translateY(-50%) translateX(-50%);
	top: 50%;
	left: 50%;
	z-index: 9;
}

@media all and (orientation : portrait) {
	.progress-bar-image {
		width: 100vw;
		height: 100vw;
	}
}
@media all and (orientation : landscape) {
	.progress-bar-image {
		width: 100vh;
		height: 100vh;
