				<div class="progress-bar-bg">
					<div id="progress-bar-content"></div>
				</div>
				<img ref="animatedGif" class="progress-bar-image" :src="`${imgUrl}/${this.brandName}/入場.webp`" alt=""
					srcset="" />
			</div>
			<div id="game-shell">
				<div id="background-img"></div>
			</div>
			<div id="game-overlay">
				<div v-if="showLoading" :class="`loading_holder ${animationLoading ? 'maskAnimate' : ''}`"
					style="width: 0px; height: 0px; overflow: unset;">
					<div class="loading_panel" id="loading_panel"
						:style="`opacity: 1; width: 360px; left: 0px; top: 0px; transform: scale(1);height:${configurationHeight}px;`">
						<div class="loading_background" style="background-color: rgb(49, 49, 61);">
						</div>
						<div class="loading_container">
							<div class="loading_circle_container">
								<div class="loading_circle" style="background-color: rgb(255, 200, 36);"></div>
								<div class="loading_circle" style="background-color: rgb(255, 200, 36);"></div>
								<div class="loading_circle" style="background-color: rgb(255, 200, 36);"></div>
							</div>
							<div class="loading_font" style="color: rgb(255, 200, 36);">A carregar</div>
						</div>
					</div>
				</div>
				<div id="game-header-holder"
					style="z-index: 999; pointer-events: none; position: absolute;top:0; width: 360px; visibility: visible;">
					<div id="game-title" class="game-title"
						style="visibility: visible; justify-content: flex-start; width: 300px; height: 18px; font-size: 11.5px;">
						<div style="padding-left: 12px; display: flex; align-items: center;">{{ gameInfo.name }}</div>
					</div>
					<div id="time-stamp" class="time_stamp"
						style="justify-content: flex-end; height: 18px; font-size: 11.5px; visibility: visible; width: 360px;">
						<div style="padding-right: 12px; display: flex; align-items: center;">{{ timeStamp }}</div>
					</div>
				</div>
				<div id="splash" class="screen_color screen_compat" :style="splashStyle">
					<div id="footer-wrapper">
						<div id="ui-root">
							<div id="loading-container" class="loading-container-port">
								<div id="progress-bar-container-port" class="progress-bar-container-port">
									<div class="progress-bar-background"></div>
									<div class="progress-bar-fill-container">
										<div id="progress-bar-fill-stripes" class="progress-bar-fill stripes "
											style="background-color: rgb(195, 32, 182); width: 10%;">
											<div class="top-highlight"></div>
											<div class="front-highlight"></div>
										</div>
									</div>
									<div class="progress-bar-outline border-inner"></div>
									<div class="progress-bar-outline border-outer"></div>
								</div>
								<p id="tips-text" class="text-port"><span id="tips-text-span">Loading game
										resourcese</span><span id="tips-text-child2"
										class="tips-text-child2 tips-text-child2-hidden">[10%]</span>
								</p>
							</div>

							<div id="animationTipsContainer" class="animationTipsContainer-port">
								<div class="animated_text_wrap">
									<div id="animated_text"
										class="animated_text animated-text-reset-to-bottom-port animated_text_move_to_center">
										Update to latest
										device update for best gaming experience!</div>
								</div>
							</div>
							<div id="__startedButton"
								class="start-button-container-port start-button start-button-show-port"
								style=" background-color: rgb(195, 32, 182); text-shadow: rgb(195, 32, 182) 0px 1px 2px; visibility: hidden;">
								<div class="start-button-inner">
								</div>
								<p id="get-started-text" class="text-port">Começar</p>
							</div>
						</div>
						<img id="footer-text-img" class="footer-text-img" :src="`${imgUrl}/${brandName}/copyright.png`">
						<img id="footer-copyright-image-id" class="footer-copyright-image" :src="`${imgUrl}/${brandName}/website.png`">
					</div>
				</div>
			</div>
			<div id="initial-loader-1">
				 <img class="initial-loader-img" :src="`${imgUrl}/${brandName}/循環.webp`" alt="" srcset="" />
			</div>
		</div>
	</div>
</template>

<script>
export default {
	computed: {
		splashStyle() {
			const lang = this.$i18n.locale
			const imgMap = {
				en: require('@/assets/image/en/startup.jpg'),
				pt: require('@/assets/image/pt/startup.jpg')
			}
			const bgUrl = imgMap[lang] || imgMap['en']

			return {
				position: 'absolute',
				top: 0,
				left: 0,
				// height: '360px',
				// width: `${this.configurationHeight}px`, // 使用动态窗口尺寸
				backgroundColor: '#171717',
				backgroundImage: `url('${bgUrl}')`,
				backgroundRepeat: 'no-repeat',
				backgroundPosition: 'center',
				backgroundSize: 'cover'
			}
		}
	},
	components: {
	},
	data() {
		return {
			gameId: null,
			timeStamp: '00:00:00',
			configurationHeight: '640',
			showHistory: false,
			hideHistory: false,
			showLoading: false,
			animationLoading: true,
			trophiesCoordinateBottom: 120,
			trophiesCoordinateLeft: 360,
			gameInfo: {
				jbicon_pos: "-999,-999",
				name: ""
			},
			isTrophiesIconShow: false,
			animationDuration: 0.75,
			brandName: 'kf',
			imgUrl: process.env.VUE_APP_IMG_URL || 'https://testcdnupload.pg-gub.com/common',
		}
	},
	created() {
		this.brandName = this.getParams("brandName") || 'kf'
	},
	mounted() {
		// 监听窗口大小变化
		window.addEventListener('resize', this.updateWindowSize);

		let nav1 = document.createElement('script')
		nav1.type = 'text/javascript'
		nav1.src = require('@/assets/js/view.js')

		let nav = document.createElement('script')
		nav.type = 'text/javascript'
		nav.src = require('@/assets/js/game_index.js')

		setInterval(this.getCurrentTime, 1000)

		this.gameId = this.getParams("game_id")
		if (this.gameId) {
			this.getGame()
		}

		window.token = this.getParams("token")
		window.gameId = this.gameId

		localStorage.setItem("token", window.token)
		localStorage.setItem("gameId", window.gameId)

		window.toVerifyByOrderNo = this.toVerifyByOrderNo
		this.configurationHeight = window.configurationHeight

		this.reloadAnimation();
	},
	beforeDestroy() {
		window.removeEventListener('resize', this.updateWindowSize);
	},
	methods: {
		reloadAnimation() {
			const img = this.$refs.animatedGif;
			const originalSrc = img.src;

			// 清空src再重新设置
			img.src = 'data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==';

			// 使用setTimeout确保重载生效
			setTimeout(() => {
				img.src = `${originalSrc}?t=${Date.now()}`;
			}, 50);
		},
		updateWindowSize() {
			this.configurationHeight = window.configurationHeight
		},
		toVerifyByOrderNo(orderNo) {
			localStorage.setItem("orderNo", orderNo)
			let routeData = this.$router.resolve({
				name: "verify",
			});
			let uuid = this.guid().toUpperCase()
			let link =
				`https://verify.pgsoft.game/?atk=${uuid}&gid=68&env=13&l=pt&sid=1837027255981835264&tid=RCBQTF23&token=${token}&game_id=${window.gameId}&order_no=${orderNo}`
			window.open(link + routeData.href, '_blank');
		},
		guid() {
			return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
				var r = Math.random() * 16 | 0,
					v = c == 'x' ? r : (r & 0x3 | 0x8);
				return v.toString(16);
			});
		},
		// 加载时调用 加载后调用
		changeLoading() {
			if (this.showLoading) {
				this.animationLoading = false
			}
			this.showLoading = !this.showLoading
		},
		getCurrentTime() {
			//获取当前时间并打印
			var _this = this;
			let hh = new Date().getHours();
			let mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes();
			let ss = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds();
			_this.timeStamp = hh + ':' + mf + ':' + ss;
		},
		getGame() {
			this.$http.get('/api/game/get_config', {
				params: {
					game_id: this.gameId
				}
			}).then(response => {
				if (response.data.meta.code == 0) {
					this.gameInfo = response.data.data
					if (this.gameInfo.jbicon_pos.length) {
						let trophiesCoordinate = this.gameInfo.jbicon_pos.split(",")
						this.trophiesCoordinateBottom = trophiesCoordinate[1]
						this.trophiesCoordinateLeft = trophiesCoordinate[0]
					}
					window.sessionStorage.setItem("trophiesCoordinateLeft", this.trophiesCoordinateLeft);
					window.sessionStorage.setItem("trophiesCoordinateBottom", this.trophiesCoordinateBottom);
				}
			});
		},
		getParams(name) {
			var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
			var r = decodeURI(window.location.search).substr(1).match(reg);
			if (r != null) {
				return unescape(r[2]);
			} else {
				return null;
			}
		},
	}
}
</script>