function calcSize() {
	let finalWidth = 0,
		finalHeight = 0;
        
	let transform, transformOrigin, height, width;

	let isWindowLandscape = window.innerHeight > window.innerWidth
	if (isWindowLandscape && isMobile()) {
		height = window.innerWidth;
		width = window.innerHeight;
		transform = 'rotate(90deg)';
		transformOrigin = '0px 0px 0px';
	} else {
		width = window.innerWidth;
		height = window.innerHeight;
		transform = 'rotate(0deg)';
	}

	const curRatio = height / width;
	const minRatio = 768 / 1815;
	const maxRatio = 768 / 1366;
	if (curRatio < minRatio) {
		finalHeight = height;
		finalWidth = height / minRatio;
	} else if (curRatio > maxRatio) {
		finalWidth = width;
		finalHeight = width * maxRatio;
	} else {
		finalHeight = height;
		finalWidth = width;
	}

	window.configurationHeight = `${(finalWidth/finalHeight)*360}`
	window.paytableContentHeight = `${(finalWidth/finalHeight)*360-63}`

	return {
		width: finalWidth,
		height: finalHeight,
		transform: transform,
		transformOrigin: transformOrigin,
		top: (height - finalHeight) / 2,
		left: (width - finalWidth) / 2
	};
}

window.resetGameView = function resetGameView() {
	// const div = document.getElementById("game-box");
	// if (!div) {
	// 	return;
	// }

	const size = calcSize();
	// div.style.height = `${size.height}px`;
	// div.style.width = `${size.width}px`;
	// div.style.top = `${size.top}px`;
	// if (size.transformOrigin) {
	// 	div.style.right = `${size.left}px`;
	// 	div.style.transformOrigin = size.transformOrigin
	// } else {
	// 	div.style.left = `${size.left}px`;
	// }
	// div.style.transform = size.transform;

	const screen = document.getElementById("screen");
	if (size.transformOrigin) {
		screen.style.transform = size.transform + " translateY(-100%)";
		screen.style.height = window.innerWidth + "px";
		screen.style.width = window.innerHeight + "px";
		// screen.style.right = `${size.left}px`;
		screen.style.transformOrigin = size.transformOrigin
	} else {
		screen.style.transform = size.transform;
		screen.style.height = window.innerHeight + "px";
		screen.style.width = window.innerWidth + "px";
		// screen.style.left = `${size.left}px`;
	}

	const initialLoader = document.getElementById("initial-loader-1");
	if (initialLoader) {
		if (size.transformOrigin) {
			initialLoader.style.height = window.innerWidth + "px";
			initialLoader.style.width = window.innerHeight + "px";
		} else {
			initialLoader.style.height = window.innerHeight + "px";
			initialLoader.style.width = window.innerWidth + "px";
		}
	}


	const bgCover = document.getElementById("progress-bar-bg-cover");
	if (bgCover) {
		if (size.transformOrigin) {
			bgCover.style.height = window.innerWidth + "px";
			bgCover.style.width = window.innerHeight + "px";
		} else {
			bgCover.style.height = window.innerHeight + "px";
			bgCover.style.width = window.innerWidth + "px";
		}
	}

	// 后面阴影
	const shadowDiv = document.getElementById("canvas-shadow");
	if (shadowDiv) {
		shadowDiv.style.height = `${size.height}px`;
		shadowDiv.style.width = `${size.width}px`;
		shadowDiv.style.top = `${size.top}px`;
		shadowDiv.style.left = `${size.left}px`;
	}

	const overlay = document.getElementById("game-overlay");
	if (!overlay) {
		return;
	}
	overlay.style.top = `${size.top}px`;
	overlay.style.left = `${size.left}px`;


	let splash = document.getElementById('splash')
	splash.style.height = `${size.height}px`;
	splash.style.width = `${size.width}px`;


	let gameHistory = document.getElementById('game-history-container')
	if (gameHistory) {
		gameHistory.style.height = `${(size.width/size.height)*360}px`;
	}

	let paytableContainer = document.getElementById('paytable-container')
	if (paytableContainer) {
		paytableContainer.style.height = `${(size.width/size.height)*360}px`;
	}
	let paytableContent = document.getElementById('paytable-content')
	if (paytableContent) {
		paytableContent.style.height = `${(size.width/size.height)*360-63}px`;
	}

	let gameRulesContainer = document.getElementById('game-rules-container')
	if (gameRulesContainer) {
		gameRulesContainer.style.height = `${(size.width/size.height)*360}px`;
	}
	let rulesContent = document.getElementById('rulesContent')
	if (rulesContent) {
		rulesContent.style.height = `${(size.width/size.height)*360-63}px`;
	}

	let ui_block = document.getElementById('ui_block')
	if (ui_block) {
		ui_block.style.height = `${(size.width/size.height)*360}px`;
	}
	let ghVrfPopupAnimBG = document.getElementById('ghVrfPopupAnim-BG')
	if (ghVrfPopupAnimBG) {
		ghVrfPopupAnimBG.style.height = `${(size.width/size.height)*360}px`;
	}


	let loadingPanel = document.getElementById('loading_panel')
	if (loadingPanel) {
		loadingPanel.style.height = `${(size.width/size.height)*360}px`;
	}

	let screenSafeAreaHeight = ((size.height / size.width) * 360 - 640) / 2
	let socialWidgetContainer = document.getElementById('social_widget-container')
	let trophiesBottom = window.sessionStorage.getItem("trophiesCoordinateBottom");
	trophiesBottom = parseInt(trophiesBottom) + parseInt(screenSafeAreaHeight)
	let trophiesLeft = window.sessionStorage.getItem("trophiesCoordinateLeft");
	if (socialWidgetContainer != null) {
		socialWidgetContainer.style.transform = `translate(${trophiesLeft}px,${trophiesBottom}px)`
	}

	let genericBackgroundView = document.getElementById('footer-main-tournament-view')
	if (genericBackgroundView != null) {

		genericBackgroundViewLeft = 0;
		genericBackgroundViewBottom = (size.height / size.width) * 360 - 640;
		genericBackgroundView.style.transform =
			`translate(${genericBackgroundViewLeft}px,${genericBackgroundViewBottom}px)`
	}


}

resetGameView();

window.addEventListener("resize", resetGameView);


function isMobile() {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
}
